('/*add some style, need to scope (using the typeIdentifier as class prefix) every style declaration to prevent collision */\n\n.pointAndLineFunctionInteraction {\n    width: 100%;\n    margin: 20px 0;\n    padding: 20px;\n    border : 6px solid #f5f3f2;\n}\n\n.pointAndLineFunctionInteraction .shape-controls .clearfix {\n    overflow: auto;\n}\n\n.pointAndLineFunctionInteraction .shape-panel {position:relative; display: inline-block; width: 100%;}\n\n.pointAndLineFunctionInteraction .shape-panel .templates {display:none;}\n\n.pointAndLineFunctionInteraction .shape-panel .shape-container {float:left;}\n\n.pointAndLineFunctionInteraction .shape-panel .shape-controls {float:left; margin-bottom:20px;}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container {width:200px; margin:3px 0; display:block; position:relative; padding: 3px;}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container .btn {width:150px; float:left;}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container .triangle{\n    width: 0;\n    height: 0;\n    border-style: solid;\n    border-width: 16px 0 16px 27.7px;\n    border-color: transparent transparent transparent #266d9c;\n    float:left;\n    top:2px;\n    position: relative;\n    visibility: hidden;\n}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container.activated .triangle{\n    visibility: visible;\n}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container .btn:hover + .triangle {opacity:.85;}\n\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container .line-styles {display:none; margin-top:30px; padding:10px;width:150px;border: solid 3px #EFEFEF;}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container .line-style {display:inline-block; margin-left:3px;}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container svg {top:5px;}\n\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container.deactivated .btn {background : #AFB4BB !important; text-shadow:none;}\n.pointAndLineFunctionInteraction .shape-panel .shape-controls .button-container.deactivated .btn:hover {opacity:1; cursor:default;}\n');
